.container {
  display: flex;
  flex-direction: column;
  border-radius: 0.625rem;
  padding: 0.9375rem;
  background-color: var(--white);
  font-size: 0.875rem;
  box-shadow: 0.3125rem 0.3125rem black;
}
@media (min-width: 40em) {
  .container {
    max-width: 17.5rem;
  }
}
.container__img {
  width: 100%;
  border-radius: 0.625rem;
  margin-bottom: 0.625rem;
}
.container__learning {
  width: 4.375rem;
  font-weight: 800;
  border-radius: 0.125rem;
  background-color: var(--yellow);
  margin: 0.3125rem 0;
  padding: 0.3125rem;
}
.container__label {
  color: var(--gray);
  font-size: 0.875rem;
}
.container__para {
  color: var(--gray);
  margin-top: 0;
}
.container span {
  display: flex;
}
.container span img {
  height: 1.875rem;
  width: 1.875rem;
  align-self: center;
}
.container span p {
  text-indent: 0.75rem;
  font-weight: 800;
}